import React from "react";
import Lesson from "./Lesson";
import { List, ListItem } from "material-ui/List";
// import ContentInbox from 'material-ui/svg-icons/content/inbox';
import ActionGrade from "material-ui/svg-icons/action/grade";
import ActionClass from "material-ui/svg-icons/action/class";

class Section extends React.Component {
  render() {
    let lessonsJSON = this.props.section.lessons;
    const { orderNum, courseOrderNum } = this.props;

    let lessons = Object.keys(lessonsJSON).map(function(key, index) {
      var lesson = lessonsJSON[key];
      return (
        <Lesson
          key={index}
          lesson={lesson}
          orderNum={index + 1}
          sectionOrderNum={orderNum}
          courseOrderNum={courseOrderNum}
        />
      );
    });

    const { sectionTitle } = this.props.section;

    return (
      <ListItem
        style={{ backgroundColor: "#eee", marginLeft: "60px" }}
        primaryText={`${this.props.orderNum}. ${sectionTitle}`}
        leftIcon={<ActionClass />}
        initiallyOpen={false}
        primaryTogglesNestedList={true}
        nestedItems={lessons}
      ></ListItem>
    );
  }
}

export default Section;



