import React from "react";
import Course from "./Course";
// import { coursesJSON } from '../data'
import { coursesJSON } from "../fullLessons";

import { List, ListItem } from "material-ui/List";
import ActionGrade from "material-ui/svg-icons/action/grade";
import ContentInbox from "material-ui/svg-icons/content/inbox";
import ContentDrafts from "material-ui/svg-icons/content/drafts";
import ContentSend from "material-ui/svg-icons/content/send";
import Subheader from "material-ui/Subheader";
import Toggle from "material-ui/Toggle";

class CourseList extends React.Component {
  state = {
    open: false
  };

  handleToggle = () => {};

  handleNestedListToggle = item => {};

  render() {
    let courses = Object.keys(coursesJSON).map(function(key, index) {
      var course = coursesJSON[key];
      return <Course key={index} course={course} orderNum={index + 1} />;
    });

    return (
      <div>
        <List>
          <Subheader>List of Courses</Subheader>
          {courses}
        </List>
      </div>
    );
  }
}

export default CourseList;

