import React from "react";

class Lesson extends React.Component {
  render() {
    const {
      lessonTitle,
      lessonType,
      lessonGithubLink,
      lessonLearnCoLink
    } = this.props.lesson;
    const { orderNum, sectionOrderNum, courseOrderNum } = this.props;
    return (
      <div style={{ margin: "3px 10px 3px 120px" }}>
        <span style={{ marginRight: "5px" }}>
          {/* {lessonType === "readme" ? ( */}
          {!(
            lessonTitle.toLowerCase().includes("lab") ||
            lessonTitle.toLowerCase().includes("project")
          ) ? (
            <svg
              xmlns="http://www.w3.org/2000/svg"
              width="10"
              height="10"
              viewBox="0 0 24 24"
            >
              <path d="M4 6H2v14c0 1.1.9 2 2 2h14v-2H4V6zm16-4H8c-1.1 0-2 .9-2 2v12c0 1.1.9 2 2 2h12c1.1 0 2-.9 2-2V4c0-1.1-.9-2-2-2zm-1 9H9V9h10v2zm-4 4H9v-2h6v2zm4-8H9V5h10v2z">
                <title>{"readme"}</title>
              </path>
            </svg>
          ) : (
            <svg
              xmlns="http://www.w3.org/2000/svg"
              width="10"
              height="10"
              viewBox="0 0 24 24"
            >
              <path d="M22.7 19l-9.1-9.1c.9-2.3.4-5-1.5-6.9-2-2-5-2.4-7.4-1.3L9 6 6 9 1.6 4.7C.4 7.1.9 10.1 2.9 12.1c1.9 1.9 4.6 2.4 6.9 1.5l9.1 9.1c.4.4 1 .4 1.4 0l2.3-2.3c.5-.4.5-1.1.1-1.4z">
                <title>{"lab"}</title>
              </path>
            </svg>
          )}
        </span>
        <a target="_blank" href={lessonGithubLink}>
          <h5
            style={{ color: "green", display: "inline", marginRight: "10px" }}
          >
            {`${lessonTitle}  |  ${courseOrderNum}-${sectionOrderNum}-${orderNum}`}
          </h5>
        </a>
        <a
          target="_blank"
          href={lessonLearnCoLink}
          style={{ color: "lightblue", fontSize: "12px" }}
        >
          {" "}
          learn.co{" "}
        </a>
      </div>
    );
  }
}

export default Lesson;



