import React from "react";
import Section from "./Section";
import { List, ListItem } from "material-ui/List";
import ContentInbox from "material-ui/svg-icons/content/inbox";
import ActionGrade from "material-ui/svg-icons/action/grade";

class Course extends React.Component {
  render() {
    let sectionsJSON = this.props.course.sections;
    const orderNum = this.props.orderNum;
    let sections = Object.keys(sectionsJSON).map(function(key, index) {
      var section = sectionsJSON[key];
      return (
        <Section
          key={index}
          section={section}
          orderNum={index + 1}
          courseOrderNum={orderNum}
        />
      );
    });

    const { courseTitle } = this.props.course;

    return (
      <ListItem
        primaryText={`${this.props.orderNum}. ${courseTitle}`}
        leftIcon={<ContentInbox />}
        initiallyOpen={false}
        primaryTogglesNestedList={true}
        nestedItems={sections}
      ></ListItem>
    );
  }
}

export default Course;



